nav.main-nav {

  position: fixed;
  top: 0;
  right: -250px;
  display: block;
  transition: .3s;
  width: 250px;
  height: 100vh;
  background: #343434;
  z-index: 1;
  overflow: auto;

  ul {

    list-style: none;
    padding: 0 0 0 15px;
    margin-top: 23px;

    li {

      text-transform: uppercase;
      height: 30px;
      font-weight: 600;

      a {
        display: inline-block;
        margin: auto;
        width: 80%;
        color: grey;
      }
      a:hover {
        text-decoration: none;
        color: $orange;
      }

    }

    li.active {

      a {
        color: white;
      }

    }

  }

  div.social-media {

    align-items: flex-start;
    border-bottom: 1px solid #414141;
    border-top: 1px solid #414141;
    display: flex;
    flex-direction: column;
    justify-content: center;
    overflow: hidden;
    padding: 15px 15px;

    div.fb-like {
      margin-bottom: 10px;
    }

    div.google-like {
    }

  }

}

nav.active {
  right: 0;
}

@media screen and (min-width: 990px) {


  nav.main-nav {

    position: relative;
    height: 60px;
    background: transparent;
    right: 0;
    top: 0;
    width: 80%;
    float: right;

    ul {

      height: 100%;
      margin: 0;
      padding: 0;
      text-align: right;

      li {

        display: inline-block;
        font-weight: lighter;
        font-size: 16px;
        height: 60px;
        line-height: 60px;
        padding: 0 10px;
        margin-left: -4px;
        border-right: 1px solid $light-border;
        box-sizing: border-box;

        a {
          display: block;
          color: black;
          width: 100%;
        }

        a:hover {
          color: black;
        }

      }

      li.active {
        background: white;

        a {
          color: black;
        }

      }

      li:hover {
        background: white;
      }

    }

    div.social-media {
      display: none;
    }

    ul:last-child {
      display: none;
    }

  }

}


