main.gallery {

  section.slider {
    width: 100%;
    height: 300px;
    background: url('../../images/water-back.jpg') 100% no-repeat;
    background-size: cover;
  }

  section.gallery-wrap {

    margin: 30px 0 50px 0;

    div.container {

      div.row {

        div.heading {

          padding: 0 10px;
          text-align: center;

          h1 {
            color: $heading-blue;
            font-weight: 500;
            text-transform: uppercase;
          }

          p {
            color: $light-border;
            font-weight: 200;
          }

          hr {
            height: 2px;
            width: 40px;
            background: #bababa;
            margin: 0px auto 15px auto;
          }

        }

        a {
          box-sizing: border-box;
          margin-top: 10px;
          padding: 0 10px;
        }

      }

    }

  }

}

@media screen and (min-width: 990px) {

  main.gallery {

    section.gallery-wrap {

      margin: 30px 0 50px 0;

      div.container {

        div.row {

        }

      }

    }

  }

}
