main.homepage {

  section {

    padding: 20px 0;

    div.container {

      div.heading {

        padding: 0 10px;
        text-align: center;

        h1 {
          color: $heading-blue;
          font-size: 2.5em;
          font-weight: 500;
          text-transform: uppercase;
        }

        hr {
          height: 2px;
          width: 40px;
          background: #bababa;
          margin: 0px auto 15px auto;
        }

      }

    }

  }

  section.slider {

    width: 100%;
    background: url('../../images/background.jpg') 100% no-repeat;
    background-size: cover;
    padding: 0;

    img {
      width: 50%;
      height: auto;
      margin: auto;
    }
  }

  section.about {

    div.container {

      div.box {

        display: flex;
        flex-direction: row;
        align-items: flex-start;

        div.icon {

          box-sizing: border-box;
          display: inline-block;
          padding: 15px 5px;
          width: 20%;
          height: 100%;
          align-self: center;

          img {
            width: 100%;
            height: auto;
          }

        }

        div.text {

          box-sizing: border-box;
          display: inline-block;
          padding: 15px;
          width: 70%;

          p {

            font-size: 18px;
            font-weight: 200;
            line-height: 18px;
            color: #777777;

            strong {
              color: $orange;
            }

          }

        }

      }

    }

  }

  section.water-well {

    div.container {

      div.box {

        text-align: center;

        div.text {

          display: inline-block;
          padding: 15px;

          p {

            font-size: 18px;
            font-weight: 200;
            line-height: 18px;
            color: #777777;
            width: 65%;
            margin: auto;

            strong {
              color: $orange;
            }

          }

        }
      }

    }

  }


  section.articles {

    div.container {

      div.row {

        ul {

          list-style-type: none;
          width: 100%;
          padding: 0;
          margin-top: 20px;

          li {

            display: inline;
            text-align: center;

            article {

              width: 100%;
              padding: 15px 20%;
              margin: auto;
              background: #f5f5f5;
              position: relative;
              text-align: center;

              i {
                position: relative;
                top: -30px;
                line-height: 60px;
                height: 60px;
                width: 60px;
                font-size: 24px;
                background: #f5f5f5;
                border-radius: 50%;
                display: inline-block;
                transition: .5s;
              }

              h2 {
                margin: 0 0 15px 0;
                font-weight: 300;
                font-size: 22px;
              }

              a {
                color: $orange;
              }

            }

          }

          li:hover {

            article {

              background: transparent;

              i {
                background: $orange;
                color: white;
              }

            }

          }

        }

      }

    }

  }

  section.location {

    text-align: center;

    div.container {

      img {
        display: inline-block;
        margin: auto;
        width: 100%;
        height: auto;
      }

      p {

        font-size: 16px;
        font-weight: 200;

        i {
          color: $main-blue;
          font-size: 24px;
          font-weight: 400;
        }

      }

    }

  }

}

@media screen and (min-width: 990px) {

  main.homepage {

    section {

    }

    section.location {

      div.container {

        img {
          width: 50%;
        }

      }

    }

    section.slider {

      img {
        width: 30%;
      }

    }

    section.water-well {

      div.container {

        div.box {

          div.text {

          }

          div.icon {

            img {
              width: 70%;
            }

          }

        }

      }

    }

  }

}
