main.production {

  section.slider {
    width: 100%;
    height: 300px;
    background: url('../../images/water-back.jpg') 100% no-repeat;
    background-size: cover;
    margin-bottom: 20px;
  }

  section.offers {

    div.container {

      div.row {

        div.heading {

          padding: 0 10px;
          text-align: center;

          h1 {
            color: $heading-blue;
            font-weight: 500;
            text-transform: uppercase;
          }

          hr {
            height: 2px;
            width: 40px;
            background: #bababa;
            margin: 0px auto 15px auto;
          }

        }

        div.offer {

          display: flex;
          flex-direction: row;
          align-items: flex-start;

          div.icon {

            box-sizing: border-box;
            display: inline-block;
            padding: 15px 5px;
            height: 100%;
            width: 15%;
            align-self: center;

            img {
              width: 100%;
              height: auto;
            }

          }

          div.text {

            width: 80%;
            padding-left: 20px;

            h3 {
              color: $orange;
              font-weight: 300;
            }

            hr {
              width: 25px;
              height: 2px;
              margin: 0 100% 15px 0;
              background: $orange;
            }

            p {
              color: grey;
              font-weight: 200;
              font-size: 15px;
            }

          }

        }

      }

    }

  }

  section.contact-slider {

    margin-top: 30px;
    background: url('../../images/water.jpg') no-repeat;
    background-size: cover;
    text-align: center;

    div.interest {
      padding-bottom: 30px;

      div.row {

        div:first-child {

          h2 {
            margin-top: 40px;
            color: black;
            font-weight: 300;
          }

          p {
            color: $orange;
            font-size: 16px;
            font-weight: 200;
          }

        }

        div:last-child {


          a {
            background: rgba(0,0,0,0.2);
            border: 2px solid rgba(0,0,0,0.3);
            border-radius: 5px;
            box-sizing: border-box;
            color: white;
            display: inline-block;
            line-height: 10px;
            padding: 20px;
            text-align: center;
            text-decoration: none;
            width: 60%;
          }

          a:hover {
            background: rgba(0,0,0,0.3);
          }

        }

      }

    }

  }

}

@media screen and (min-width: 990px) {

  main.production {

    section.contact-slider {

      text-align: left;

      div.interest {

        padding-bottom: 0;

        div.row {

          div:first-child {

            h2 {

            }

            p {
            }

          }

          div:last-child {
            line-height: 150px;
          }

        }

      }

    }

  }
}
