/* Header */
header {

  background: transparent;
  height: 50px;
  position: fixed;
  width: 100%;
  z-index: 1;

  span.nav-trigger {

    color: grey;
    display: block;
    font-size: 22px;
    height: 50px;
    position: fixed;
    right: 0;
    background: #343434;
    text-align: center;
    transition: .3s;
    width: 50px;
    z-index: 101;

    i {
      line-height: 50px;
    }

  }

  span:hover {
    background: #444;
  }

  span.active {
    right: 250px;
  }

  div.container {

    div.row {

      div.logo {

        h1 {
          margin: 0;
          line-height: 60px;
        }

      }

    }

  }

}

@media screen and (min-width: 990px) {

  header {

    background: #f5f5f5;
    box-shadow: 0 1px 3px rgba(34, 25, 25, 0.4);
    height: 60px;
    z-index: 1;

    span.nav-trigger {
      display: none;
    }

  }

}
