/* Footer */
footer {

  section.main-footer {

    background: #292929;
    color: white;
    padding-top: 10px;

    div.container {

      div.row {

        div.footer-box {

          padding: 5px;

          div.google-like {
            margin-top: 10px;
          }

          span {
            font-size: 12px;
            color: #828282;
            margin-left: 3px;
          }

          p {
            color: #828282;
            font-weight: 200;
          }

          i {
            color: white;
            margin-right: 10px;
          }

          h4 {
            display: inline-block;
            font-weight: 300;
            margin: 0;
          }

          hr {
            width: 45px;
            height: 2px;
            background: $orange;
            margin: 10px 100% 15px 0;
            border: none;
          }

        }
      }
    }
  }

  section.bottom-footer {

    background: #171717;
    height: 40px;

    div.container {
      line-height: 40px;
    }

  }

}
