main.contact {

  section.slider {

    width: 100%;
    height: 300px;

    div#map {
      margin: auto;
      width: 100%;
      height: 100%;
    }

  }

  section.contact-wrap {

    margin: 30px 0 50px 0;

    div.container {

      div.row {

        text-align: center;

        div.heading {

          text-align: center;
          padding: 0 10px;

          h2 {
            color: $heading-blue;
            font-weight: 500;
            text-transform: uppercase;
          }

          p {
            color: #444;
            font-size: 18px;
            font-weight: 200;
          }

          hr {
            height: 2px;
            width: 40px;
            background: #bababa;
            margin: 0px auto 15px auto;
          }

        }

        a.box {

          box-sizing: border-box;
          padding: 15px;
          color: black;
          text-decoration: none;
          cursor: pointer;

          div.wrapper {

            width: 100%;
            margin: 25px auto 0 auto;
            background: #f5f5f5;
            transition: .4s;

            div.icon {

              text-align: center;
              padding: 0;

              span {
                display: inline-block;
                position: relative;
                background: #f5f5f5;
                margin: -60px auto 0 auto;
                width: 50px;
                height: 50px;
                border-radius: 50%;
                transition: .4s;

                i {
                  font-size: 25px;
                  line-height: 50px;
                }

              }

            }

            div.text {

              text-align: center;
              padding-bottom: 15px;

              h4 {
                font-weight: 300;
              }

              span {
                color: $orange;
                font-weight: 200;
                font-size: 15px;
              }

            }

          }

        }

        a:hover {

          div.wrapper {

            background: transparent;

            div.icon {

              span {
                background: $orange;
                color: white;
              }

            }
          }
        }

        div.form {

          form {

            width: 90%;
            margin: auto;

            div {

              padding: 0;

              input, textarea, button {
                resize: none;
                margin: 2px 0;
                background: transparent;
                border: 1px solid #d3d3d3;
                border-radius: 3px;
                padding: 8px 10px;
                width: 100%
              }

              button {

                background: $orange;

                span {
                  color: white;
                  font-size: 20px;
                }

              }

            }

          }

        }

      }

    }

  }

}

@media screen and (min-width: 990px) {

  main.contact {

    section.contact-wrap {

      margin: 30px 0 50px 0;

      div.container {

        div.row {

          div.heading {

            h2 {}

            hr {}

            p {}

          }

          a.box {

            div.wrapper {

              div.icon {

                span {

                  i {}

                }

              }

              div.text {

                h4 {}

                span {}

              }

            }

          }

          div.form {

            form {
              width: 45%;
            }

          }

        }

      }

    }

  }

}
